:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  /* ------------------------- Social Networks colors ------------------------- */
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  /* instagram has a linear gradient */
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 58%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  /*this is a linear-gradient*/
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  /* Dark Theme */
  --dark-blue-bg: hsl(230, 17%, 14%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --very-dark-blue-top-bg-Pattern: hsl(232, 19%, 15%);
  --dark-desaturated-blue-card-bg: hsl(228, 28%, 20%);
  --desaturated-blue-text: hsl(228, 34%, 66%);
  --white-text: hsl(0, 0%, 100%);
  /*Light Theme */
  --white-bg: hsl(0, 0%, 100%);
  --very-pale-blue-top-bg-pattern: hsl(225, 100%, 98%);
  --light-grayish-blue-card-bg: hsl(227, 47%, 96%);
  --dark-grayish-blue-text: hsl(228, 12%, 44%);
  --very-dark-blue-text: hsl(230, 17%, 14%);
  /* -------------------------- Default Theme configurations -------------------------- */
}

@media (prefers-color-scheme: light) {
  :root {
    --background: var(--white-bg);
    --card-color-bg: var(--very-pale-blue-top-bg-pattern);
    --over-card-color: var(--light-grayish-blue-card-bg);
    --g-text-color: var(--very-dark-blue-text);
    --card-txt-color: var(--dark-grayish-blue-text);
    --toggle-txt: var(--toggle);
    --very-contrasted-txt-color: var(--very-dark-blue-text);
    --toggle-bg: var(--toggle);
  }
}
body.light {
  --background: var(--white-bg);
  --card-color-bg: var(--very-pale-blue-top-bg-pattern);
  --over-card-color: var(--light-grayish-blue-card-bg);
  --g-text-color: var(--very-dark-blue-text);
  --card-txt-color: var(--dark-grayish-blue-text);
  --toggle-txt: var(--toggle);
  --very-contrasted-txt-color: var(--very-dark-blue-text);
  --toggle-bg: var(--toggle);
}

body.dark {
  --background: var(--dark-blue-bg);
  --card-color-bg: var(--dark-desaturated-blue-card-bg);
  --over-card-color: var(--dark-card-hover);
  --g-text-color: var(--white-text);
  --card-txt-color: var(--desaturated-blue-text);
  --very-contrasted-txt-color: var(--white-text);
  --toggle-txt: var(--white-text);
  --toggle-bg: var(--toggle-bg-start);
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--g-text-color);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--card-txt-color);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3.125rem;
  border: none;
  margin: 0;
  /*

    &[for="dark"] {
      grid-column: 1 / 2;
      justify-self: end;
    }
    &[for="system"] {
      grid-column: 1 / 4;
      grid-row: 2/3;
      justify-self: center;
      align-self: center;
    }
    &[for="light"] {
      grid-column: 3/4;
    }
  }
  */
}
.toggle label {
  font-size: 0.875rem;
  color: var(--toggle-txt);
  font-weight: 700;
  margin-right: 0.5rem;
}
.toggle label[for=dark] {
  line-height: 1.5rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem;
  opacity: 0;
  margin: 0;
  width: 1.375rem;
  height: 1.375rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 0.125rem solid var(--white-bg);
}
.toggle__background {
  display: block;
  pointer-events: none;
  height: 100%;
  width: 100%;
  border-radius: 0.75rem;
  overflow: hidden;
  background: var(--toggle-bg);
  position: absolute;
  top: 0;
}
.toggle__button {
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle);
  position: absolute;
  left: 0.1875rem;
  right: 100%;
  top: 0.1875rem;
  background-color: var(--background);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  right: 0;
  left: calc(100% - 1.3125rem);
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translateX(-50%);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  flex: 1;
  background: var(--card-color-bg);
  color: var(--card-txt-color);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  text-align: center;
  position: relative;
  overflow: hidden;
}
.card:hover {
  background-color: var(--over-card-color);
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
  height: 1.5625rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-end) 0%, var(--instagram-middle) 50%, var(--instagram-start) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__count {
  color: var(--very-contrasted-txt-color);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  color: var(--card-txt-color);
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  font-weight: normal;
  text-transform: uppercase;
  margin-bottom: 1.5625rem;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  /*@extend .card__change; */
  color: var(--lime-green);
}
.card__change--down {
  color: var(--bright-red);
}
.card__change img {
  margin-right: 0.25rem;
}
/*
I tink it was a bit hard coded, but i wanted for the design to be
as more closed as possible to the template
*/
@media (min-width: 71.875em) {
  .overview-section {
    display: grid;
    grid-template-areas: "c1 c2 c5 c6" "c3 c4 c7 c8";
  }
  .overview-section .card:nth-child(1) {
    grid-area: c1;
  }
  .overview-section .card:nth-child(2) {
    grid-area: c2;
  }
  .overview-section .card:nth-child(3) {
    grid-area: c3;
  }
  .overview-section .card:nth-child(4) {
    grid-area: c4;
  }
  .overview-section .card:nth-child(5) {
    grid-area: c5;
  }
  .overview-section .card:nth-child(6) {
    grid-area: c6;
  }
  .overview-section .card:nth-child(7) {
    grid-area: c7;
  }
  .overview-section .card:nth-child(8) {
    grid-area: c8;
  }
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.3125rem;
}

.card__count {
  margin-bottom: 0;
}

.card-grid > img {
  margin-right: 0;
}

.card-grid > img.card__icon,
.card__change {
  justify-self: end;
}

.card__count,
.card__change {
  align-self: end;
}